import React, { useState, useEffect, useCallback } from 'react';
import { useNavigate } from 'react-router-dom';
import { useTheme } from '../../theme/ThemeContext';
import { apiService } from '../../services/api';
import '../css/styles.scss';
import './InvitationsPage.scss';

const InvitationsPage = () => {
  const navigate = useNavigate();
  const { isDark, toggleTheme } = useTheme();
  const [invitations, setInvitations] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [processing, setProcessing] = useState({});

  const normalizeStatus = (status) => {
    if (!status) return 'pending';
    return status.toString().toLowerCase();
  };

  const getStatusLabel = (status) => {
    const value = normalizeStatus(status);
    switch (value) {
      case 'accepted':
        return '';
      case 'declined':
        return '';
      case 'pending':
        return ' ';
      default:
        return status || '';
    }
  };

  const extractInvitations = (payload) => {
    if (!payload) return [];
    const candidates = [
      payload.invitations,
      payload.data?.invitations,
      payload.dashboard?.invitations,
      payload.user?.invitations,
      payload.results,
      payload.items,
      Array.isArray(payload) ? payload : null
    ];

    for (const candidate of candidates) {
      if (Array.isArray(candidate)) {
        return candidate;
      }
    }
    return [];
  };

  const normalizeInvitation = (raw) => {
    if (!raw) return null;
    const organization = raw.organization || raw.org || raw.organization_info || raw.organization_data || {};
    const normalizedStatus = normalizeStatus(raw.status || raw.state || raw.invitation_status);

    return {
      id: (raw.id || raw.invitation_id || raw.uuid || raw.code || '').toString(),
      organization_name:
        raw.organization_name ||
        organization.legal_name ||
        organization.legalName ||
        organization.name ||
        '',
      role: raw.role || raw.invited_role || raw.member_role || 'USER',
      status: normalizedStatus,
      created_at: raw.created_at || raw.createdAt || raw.created || null,
      expires_at: raw.expires_at || raw.expiresAt || raw.expires || null,
    };
  };

  const loadInvitations = useCallback(async (showSpinner = true) => {
    try {
      if (showSpinner) {
        setLoading(true);
      }
      setError('');
      const dashboard = await apiService.getUserDashboard();
      const rawInvitations = extractInvitations(dashboard);
      const normalized = rawInvitations
        .map((item) => normalizeInvitation(item))
        .filter(Boolean);
      setInvitations(normalized);
    } catch (err) {
      console.error('  :', err);
      setError(err?.message || '  ');
      setInvitations([]);
    } finally {
      if (showSpinner) {
        setLoading(false);
      }
    }
  }, []);

  useEffect(() => {
    loadInvitations();
  }, [loadInvitations]);

  /**   */
  const handleAccept = async (invitationId) => {
    try {
      setProcessing(prev => ({ ...prev, [invitationId]: 'accept' }));
      setError('');
      const response = await apiService.respondToInvitation(invitationId, 'accept');
      console.log('  :', response);
      await loadInvitations(false);
    } catch (err) {
      console.error('   :', err);
      const errorMessage = err?.message || '  ';
      setError(errorMessage);
      //    5 
      setTimeout(() => setError(''), 5000);
    } finally {
      setProcessing(prev => {
        const newState = { ...prev };
        delete newState[invitationId];
        return newState;
      });
    }
  };

  /**   */
  const handleDecline = async (invitationId) => {
    if (!window.confirm(' ,     ?')) {
      return;
    }

    try {
      setProcessing(prev => ({ ...prev, [invitationId]: 'decline' }));
      setError('');
      const response = await apiService.respondToInvitation(invitationId, 'decline');
      console.log('  :', response);
      await loadInvitations(false);
    } catch (err) {
      console.error('   :', err);
      const errorMessage = err?.message || '  ';
      setError(errorMessage);
      //    5 
      setTimeout(() => setError(''), 5000);
    } finally {
      setProcessing(prev => {
        const newState = { ...prev };
        delete newState[invitationId];
        return newState;
      });
    }
  };

  /**   */
  const formatDate = (dateString) => {
    if (!dateString) return ' ';
    try {
      const date = new Date(dateString);
      return date.toLocaleDateString('ru-RU', {
        day: '2-digit',
        month: '2-digit',
        year: 'numeric',
        hour: '2-digit',
        minute: '2-digit'
      });
    } catch {
      return ' ';
    }
  };

  /**    */
  const scrollToTop = () => {
    //   scrollable 
    const contentWrapper = document.querySelector('.invitations-page');
    if (contentWrapper) {
      contentWrapper.scrollTo({ top: 0, behavior: 'smooth' });
    }
    //   window  ,     body
    window.scrollTo({ top: 0, behavior: 'smooth' });
    //   document.documentElement  
    document.documentElement.scrollTo({ top: 0, behavior: 'smooth' });
  };

  if (loading) {
    return (
      <div className={`invitations-page ${isDark ? 'dark-mode' : ''}`}>
        <div className="loading"> ...</div>
      </div>
    );
  }

  return (
    <div className={`invitations-page ${isDark ? 'dark-mode' : ''}`}>
      <div className="invitations-header">
        <div style={{ display: 'flex', alignItems: 'center', gap: '15px' }}>
          <button
            className="btn-back"
            onClick={() => navigate(-1)}
            style={{
              background: 'none',
              border: 'none',
              cursor: 'pointer',
              fontSize: '18px',
              padding: '8px 12px',
              borderRadius: '6px',
              transition: 'all 0.2s ease',
              color: isDark ? '#ffffff' : '#1a1a1a'
            }}
            onMouseEnter={(e) => {
              e.target.style.backgroundColor = isDark ? 'rgba(255, 255, 255, 0.1)' : 'rgba(0, 0, 0, 0.05)';
            }}
            onMouseLeave={(e) => {
              e.target.style.backgroundColor = 'transparent';
            }}
          >
             
          </button>
          <h1> </h1>
        </div>
        <button className="btn-refresh" onClick={loadInvitations}>
          
        </button>
      </div>

      {error && (
        <div className="error-message">
          {error}
        </div>
      )}

      {invitations.length === 0 ? (
        <div className="empty-state">
          <p>    </p>
        </div>
      ) : (
        <div className="invitations-list">
          {invitations.map((invitation) => (
            <div key={invitation.id} className="invitation-card">
              <div className="invitation-info">
                <h3 className="invitation-organization">
                  {invitation.organization_name || ''}
                </h3>
                <div className="invitation-details">
                  <div className="invitation-field">
                    <span className="field-label">:</span>
                    <span className="field-value">{invitation.role || 'User'}</span>
                  </div>
                  {invitation.created_at && (
                    <div className="invitation-field">
                      <span className="field-label"> :</span>
                      <span className="field-value">{formatDate(invitation.created_at)}</span>
                    </div>
                  )}
                  {invitation.expires_at && (
                    <div className="invitation-field">
                      <span className="field-label"> :</span>
                      <span className="field-value">{formatDate(invitation.expires_at)}</span>
                    </div>
                  )}
                  {invitation.status && (
                    <div className="invitation-field">
                      <span className="field-label">:</span>
                      <span className={`field-value status-${invitation.status}`}>
                        {getStatusLabel(invitation.status)}
                      </span>
                    </div>
                  )}
                </div>
              </div>
              <div className="invitation-actions">
                <button
                  className="btn-accept"
                  onClick={() => handleAccept(invitation.id)}
                  disabled={processing[invitation.id] === 'accept' || invitation.status === 'accepted'}
                >
                  {processing[invitation.id] === 'accept' ? '...' : ''}
                </button>
                <button
                  className="btn-decline"
                  onClick={() => handleDecline(invitation.id)}
                  disabled={processing[invitation.id] === 'decline' || invitation.status === 'declined'}
                >
                  {processing[invitation.id] === 'decline' ? '...' : ''}
                </button>
              </div>
            </div>
          ))}
        </div>
      )}

      {/*   */}
      <div className="invitations-side-buttons" style={{
        position: 'fixed',
        right: '20px',
        bottom: '20px',
        display: 'flex',
        flexDirection: 'column',
        gap: '12px',
        zIndex: 1000
      }}>
        <button 
          className="side-button scroll-top" 
          onClick={scrollToTop} 
          title=""
          style={{
            width: '48px',
            height: '48px',
            borderRadius: '50%',
            border: 'none',
            background: isDark ? 'rgba(255, 255, 255, 0.1)' : 'rgba(0, 0, 0, 0.1)',
            color: isDark ? '#ffffff' : '#1a1a1a',
            fontSize: '24px',
            cursor: 'pointer',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            transition: 'all 0.2s ease',
            boxShadow: '0 2px 8px rgba(0, 0, 0, 0.15)'
          }}
          onMouseEnter={(e) => {
            e.target.style.background = isDark ? 'rgba(255, 255, 255, 0.2)' : 'rgba(0, 0, 0, 0.15)';
            e.target.style.transform = 'scale(1.1)';
          }}
          onMouseLeave={(e) => {
            e.target.style.background = isDark ? 'rgba(255, 255, 255, 0.1)' : 'rgba(0, 0, 0, 0.1)';
            e.target.style.transform = 'scale(1)';
          }}
        >
          
        </button>
        <button 
          className="side-button theme-toggle" 
          onClick={toggleTheme} 
          title=" "
          style={{
            width: '48px',
            height: '48px',
            borderRadius: '50%',
            border: 'none',
            background: isDark ? 'rgba(255, 255, 255, 0.1)' : 'rgba(0, 0, 0, 0.1)',
            cursor: 'pointer',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            transition: 'all 0.2s ease',
            boxShadow: '0 2px 8px rgba(0, 0, 0, 0.15)'
          }}
          onMouseEnter={(e) => {
            e.target.style.background = isDark ? 'rgba(255, 255, 255, 0.2)' : 'rgba(0, 0, 0, 0.15)';
            e.target.style.transform = 'scale(1.1)';
          }}
          onMouseLeave={(e) => {
            e.target.style.background = isDark ? 'rgba(255, 255, 255, 0.1)' : 'rgba(0, 0, 0, 0.1)';
            e.target.style.transform = 'scale(1)';
          }}
        >
          <img
            src={`/assets/LoginPage/${isDark ? 'sun' : 'moon'}.svg`}
            alt="Toggle theme"
            style={{ width: '24px', height: '24px' }}
          />
        </button>
      </div>
    </div>
  );
};

export default InvitationsPage;

